<template>
    <div>
        <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/classmanagement'}"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <br/>

        <h1 class="experiment-title"></h1>
        <el-input
        type="textarea"
        :rows="3"
        placeholder=" +  +  + (50)"
        v-model="classStartTime">
        </el-input>

        <h1 class="experiment-title"></h1>
        <el-input
        type="textarea"
        :rows="3"
        placeholder=" + / +  +  + (50)"
        v-model="className">
        </el-input>

        <h1 class="experiment-title"></h1>
        <el-table
        :data="teacherTableData.filter(data => !searchTeacher || data.username.toLowerCase().includes(searchTeacher.toLowerCase()))"
        style="width: 100%"
        height="400"
        highlight-current-row
        @current-change="handleSeletTeacher">

            <el-table-column
            label="ID"
            prop="userid">
            </el-table-column>

            <el-table-column
            label=""
            prop="username">
            </el-table-column>

            <el-table-column
            prop="level"
            label=""
            width="150"
            :filters="[
            { text: '', value: '' },
            { text: '', value: '' }]"
            filter-placement="bottom-end">
                <template slot-scope="scope">
                    <el-tag
                        :type="
                        scope.row.level == ''
                        ? 'warning'
                        : scope.row.level == ''
                        ? 'primary'
                        : 'primary'"
                        disable-transitions
                    >{{ scope.row.level }}</el-tag>
                </template>
            </el-table-column>

            <el-table-column
            label=""
            prop="status">
            </el-table-column>

            <el-table-column
            align="right">
                <template slot="header">
                    <el-input
                    v-model="searchTeacher"
                    size="mini"
                    placeholder=""/>
                </template>
            </el-table-column>
        </el-table>

        <h1 class="experiment-title"></h1>
        <el-table
        :data="studentTableData.filter(data => !searchStudent || data.username.toLowerCase().includes(searchStudent.toLowerCase()))"
        style="width: 100%"
        height="400"
        @selection-change="handleSelectStudent">

        
            <el-table-column
            type="selection"
            width="55">
            </el-table-column>

            <el-table-column
            label="ID"
            prop="userid">
            </el-table-column>

            <el-table-column
            label=""
            prop="username">
            </el-table-column>

            <el-table-column
            prop="level"
            label=""
            width="150"
            :filters="[
            { text: '', value: '' },
            { text: '', value: '' },]"
            filter-placement="bottom-end">
                <template slot-scope="scope">
                    <el-tag
                        :type="
                        scope.row.level == ''
                            ? 'warning'
                            : scope.row.level == ''
                            ? 'success'
                            : 'primary'"
                        disable-transitions
                    >{{ scope.row.level }}</el-tag>
                </template>
            </el-table-column>

            <el-table-column
            label=""
            prop="status">
            </el-table-column>


            <el-table-column
            align="right">
                <template slot="header">
                    <el-input
                    v-model="searchStudent"
                    size="mini"
                    placeholder=""/>
                </template>
            </el-table-column>
        </el-table>

        <h1 class="experiment-title"></h1>
        <el-table
            :data="experimentTableData.filter(data => !searchExperiment || data.experimentname.toLowerCase().includes(searchExperiment.toLowerCase()))"
            style="width: 100%"
            height="400"
            @selection-change="handleSelectExperimentChange">

            
            <el-table-column
            type="selection"
            width="55">
            </el-table-column>

            <el-table-column
            label=""
            prop="experimentno">
            </el-table-column>

            <el-table-column
            label=""
            prop="experimentname">
            </el-table-column>


            <el-table-column
            align="right">
            <template slot="header">
                <el-input
                v-model="searchExperiment"
                size="mini"
                placeholder=""/>
            </template>
            </el-table-column>
        </el-table>

        <br/>
        <br/>
        <el-button 
        type="primary" 
        plain 
        class="experiment-button"
        @click="createClass"
        ></el-button>    

    </div>
</template>

<script>
export default {
    //
    name: "CreateClass",
    //
    data(){

        return{
            //
            classInfo:{
                experimentclassno:0,
                //
                teacheridnumber:0,
                //
                classstarttime:"",
                //
                experimentclassname:"",
                //
                usersNo:[],
                //
                experimentsNo:[]
            },

            //
            searchExperiment: '',
            //
            experimentTableData:[],
            //
            experimentChoiceList:[],

            //
            searchTeacher: '',
            //
            teacherTableData:[],
            //
            teacherChoice:{},

            //
            searchStudent: '',
            //
            studentTableData:[],
            //
            studentChoice:[],

            //
            classStartTime:"",
            //
            className:""
        }
    },
    //
    methods:{
        
        //
        dataManagement(){
            //
            if("" == this.classStartTime){
                this.$message.error("")
                return false
            }
            this.classInfo.classstarttime = this.classStartTime
            //
            if("" == this.className){
                this.$message.error("")
                return false
            }
            this.classInfo.experimentclassname = this.className
            //
            if({} == this.teacherChoice){
                this.$message.error("")
                return false
            }
            this.classInfo.teacheridnumber = this.teacherChoice.userid
            //
            if(0 == this.studentChoice.length){
                this.$message.error("")
                return false
            }
            let each = 0
            for(each in this.studentChoice){
                this.classInfo.usersNo.push(this.studentChoice[each].userid)
            }
            //
            this.classInfo.usersNo.push(this.teacherChoice.userid)
            //
            if(0 == this.experimentChoiceList.length){
                this.$message.error("")
                return false
            }
            each = 0
            for(each in this.experimentChoiceList){
                this.classInfo.experimentsNo.push(this.experimentChoiceList[each].experimentno)
            }

            return true
        },

        //
        createClass(){
            //
            if(!this.dataManagement()){
                return
            }
            //
            let data = {
                method: "post",
                url:"/class/createexperimentclass",
                data: this.classInfo
            }
            //
            this.$axios.request(data)
            .then(response => {
                    //
                if(200 == response.data.code){
                    //
                    this.$message.success("");
                }
                else{
                    this.$message.error("");
                }
            })
            .catch(failResponse => {
                console.log(failResponse)
                this.$message.error("");
            })
        },

        //
        handleSelectExperimentChange(val){
            this.experimentChoiceList = val
        },

        //
        handleSeletTeacher(val){
            this.teacherChoice = val
        },

        //
        handleSelectStudent(val){
            this.studentChoice = val
            console.log(this.studentChoice)
        },

        //
        getexperimentList(){
            //
            let data = {
                method: "get",
                url: "/experiment/getexperimentlist",
            }
            //
            this.$axios.request(data)
            .then(response => {
                    //
                if(200 == response.data.code){
                    //
                    this.experimentTableData = response.data.result
                }
                else{
                    this.$message.error("");
                }
            })
            .catch(failResponse => {
                console.log(failResponse)
                this.$message.error("");
            })
        },

        //
        getTeacherList(){
            //
            let data = {
                method: "get",
                url: "/experiment/teachernoinclass",
            }
            //
            this.$axios.request(data)
            .then(response => {
                    //
                if(200 == response.data.code){
                    //
                    this.teacherTableData = response.data.result
                }
                else{
                    this.$message.error("");
                }
            })
            .catch(failResponse => {
                console.log(failResponse)
                this.$message.error("");
            })
        },

         //
         getStudentList(){
            //
            let data = {
                method: "get",
                url: "/experiment/studentnoinclass",
            }
            //
            this.$axios.request(data)
            .then(response => {
                    //
                if(200 == response.data.code){
                    //
                    this.studentTableData = response.data.result
                }
                else{
                    this.$message.error("");
                }
            })
            .catch(failResponse => {
                console.log(failResponse)
                this.$message.error("");
            })
        }
    },
    //
    mounted(){
        //
        this.getexperimentList()
        //
        this.getTeacherList()
        //
        this.getStudentList()
    }
}
</script>

<style scoped>
.experiment-title{
    font-family:"PingFang SC";
    font-size:25px;
    color:black;
    text-align:left
}
.experiment-button{
    position:relative;
    left: -44.8%;
}
</style>


