<template>
    <Contentfield>
        <div>
            <el-breadcrumb separator="/">
            <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
        </div>

        <br/>
        <el-table
            :data="tableData.filter(data => !search || data.experimentname.toLowerCase().includes(search.toLowerCase()))"
            style="width: 100%"
            height="380">
            <el-table-column
            label=""
            prop="experimentno">
            </el-table-column>
            <el-table-column
            label=""
            prop="experimentname">
            </el-table-column>
            <el-table-column
            label="ID"
            prop="userid">
            </el-table-column>
            <el-table-column
            label=""
            prop="username">
            </el-table-column>
            <el-table-column
            label=""
            prop="experimentreportno">
            </el-table-column>
            <el-table-column
            align="right">
            <template slot="header">
                <el-input
                v-model="search"
                size="mini"
                placeholder=""/>
            </template>
            <template slot-scope="scope">
                <el-button
                size="mini"
                @click="handleEdit(scope.row)"></el-button>
            </template>
            </el-table-column>
        </el-table>
    </Contentfield>
</template>

<script>
import Contentfield from "@/components/AdminCom/Contentfield.vue";
export default {
    //
    components: { Contentfield,},
    //
    name: "CorrectReport",
    //
    data(){

        return{
            //
            tableData: [],
            //
            search: '',
            //Id
            userId:this.$store.getters.userId
        }
    },
    //
    methods:{
        //
        handleEdit(row) {
            this.$router.push({path:"/correctreportdetail",query:row})
        },

        //
        getExperimentReportList() {
            //
            let data = {
                method: "get",
                url:"/experiment/getclassexperimentreportlist",
                params: {"userId": this.userId}
            }
            //
            this.$axios.request(data)
            .then(response => {
                //
               if(200 == response.data.code){
                    this.tableData = response.data.result
               }
               else{
                    this.$message.error("");
               }
            })
            .catch(failResponse => {
                this.$message.error("");
            })
        },
    },
    //
    mounted(){
        //
        this.getExperimentReportList()
    },
}
</script>

<style>

</style>


