<template>
    <div>
        <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/problemmanagement'}"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <br/>
        <el-table
            :data="tableData.filter(data => !search || data.problemdescription.toLowerCase().includes(search.toLowerCase()))"
            style="width: 100%"
            height="495">

               <!---->
            <el-table-column type="expand">
                <template slot-scope="props">
                    <el-form label-position="right" inline class="demo-table-expand">
                        <el-form-item label="">
                            <span>{{ props.row.problemno }}</span>
                        </el-form-item>
                        <el-form-item label="">
                            <span>{{ props.row.experimentno }}</span>
                        </el-form-item>
                        <el-form-item label="">
                            <span>{{ props.row.experimentname }}</span>
                        </el-form-item>
                        <el-form-item label="">
                            <span>{{ props.row.problemdescription }}</span>
                            </el-form-item>
                        <el-form-item label="A">
                            <span>{{ props.row.problemoptiona }}</span>
                        </el-form-item>
                        <el-form-item label="B">
                            <span>{{ props.row.problemoptionb }}</span>
                        </el-form-item>
                        <el-form-item label="C">
                            <span>{{ props.row.problemoptionc }}</span>
                        </el-form-item>
                        <el-form-item label="D">
                            <span>{{ props.row.problemoptiond }}</span>
                        </el-form-item>
                        <el-form-item label="">
                            <span>{{ props.row.problemanswer }}</span>
                        </el-form-item>
                    </el-form>
                </template>
            </el-table-column>

            <el-table-column
            label=""
            prop="problemno">
            </el-table-column>

            <el-table-column
            label=""
            prop="experimentname">
            </el-table-column>

            <el-table-column
            label=""
            prop="problemdescription">
            </el-table-column>

            <el-table-column
            align="right">
            <template slot="header">
                <el-input
                v-model="search"
                size="mini"
                placeholder=""/>
            </template>
            <template slot-scope="scope">
                <el-button
                size="mini"
                type="danger"
                @click="handleDelete(scope.row)"></el-button>
            </template>
            </el-table-column>
        </el-table>

        <br/>
        <!---->
        <el-button 
        type="primary" 
        plain 
        class="experiment-button"
        @click="comeToCreateProblem"
        ></el-button>

        <el-dialog
        title=""
        :visible.sync="dialogVisible"
        width="30%">
            <span></span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false"> </el-button>
                <el-button type="primary" @click="handleSureDelete"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import Contentfield from "@/components/AdminCom/Contentfield.vue";
export default {
    //
    components: { Contentfield,},
    //
    name: "ExperimentProblem",
    //
    data(){

        return{
            //
            tableData: [],
            //
            search: '',
            //
            experimentNo: 0,
            //
            experimentName:"",
            //
            dialogVisible: false,
            //
            deleteInfo:{}
        }
    },
    //
    methods:{

        //
        comeToCreateProblem(){
            let data = {
                "experimentNo": this.experimentNo,
                "experimentName": this.experimentName
            }
            this.$router.push({path:"/createproblem", query:data})
        },

        //
        handleDelete(row){
            this.deleteInfo = row
            this.dialogVisible = true
        },

        //
        handleSureDelete(){
            //
            this.dialogVisible = false
            //
            let data = {
                method: "post",
                url: "/exercise/deleteexerciseproblem",
                params: {"problemNo": this.deleteInfo.problemno}
            }
            //
            this.$axios.request(data)
            .then(response => {
                    //
                if(200 == response.data.code){
                    //
                    this.$message.success("");
                }
                else{
                    this.$message.error("");
                }
            })
            .catch(failResponse => {
                console.log(failResponse)
                this.$message.error("");
            })
        },

        //
        getProblemList(){
            //
            let data = {
                method: "get",
                url: "/exercise/getexperimentproblemlist",
                params: {"experimentNo": this.experimentNo}
            }
            //
            this.$axios.request(data)
            .then(response => {
                    //
                if(200 == response.data.code){
                    //
                    this.tableData = response.data.result
                }
                else{
                    this.$message.error("");
                }
            })
            .catch(failResponse => {
                console.log(failResponse)
                this.$message.error("");
            })
        }
    },
    //
    mounted(){
        //
        this.experimentNo = this.$route.query.experimentno
        //
        this.experimentName = this.$route.query.experimentname
        //
        this.getProblemList()
    }
}
</script>

<style scoped>
.demo-table-expand {
    font-size: 0;
  }
.demo-table-expand label {
width: 90px;
color: #99a9bf;
}
.demo-table-expand .el-form-item {
margin-right: 50px;
margin-bottom: 0;
width: 100%;
height: 100%;
}
.experiment-button{
    position:relative;
    left: -46%;
}
</style>


