<template>
    <div>
        <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/submitreport'}"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <br/>
        <div class="experiment-div">
            
            <h1 class="experiment-title">
                {{this.experimentDetail.experimentname}}
            </h1>
            <h1 class="experiment-title">
                {{this.experimentDetail.experimentno}}
            </h1>
            
            <el-row :gutter="12">  
                <el-col :span="22">
                    <h1 class="experiment-title"></h1>
                    <el-card shadow="hover">
                        <h3 class="experiment-text">{{this.experimentDetail.experimentpurpose}}</h3>
                    </el-card>
                </el-col>             
                <el-col :span="22">
                    <h1 class="experiment-title"></h1>
                    <el-card shadow="hover">
                        <h3 class="experiment-text">{{this.experimentDetail.experimentbasicknowledge}}</h3>
                    </el-card>
                </el-col>   
                <el-col :span="22">
                    <h1 class="experiment-title"></h1>
                    <el-card shadow="hover">
                        <h3 class="experiment-text">{{this.experimentDetail.experimentsteps}}</h3>
                    </el-card>
                </el-col>
                <el-col :span="22">
                    <h1 class="experiment-title"></h1>
                    <el-card shadow="hover">
                        <el-input
                        type="textarea"
                        :rows="6"
                        placeholder="(900)"
                        v-model="reportInfo.experimentphenomenon">
                        </el-input>
                    </el-card>
                </el-col>    
                <el-col :span="22">
                    <h1 class="experiment-title"></h1>
                    <el-card shadow="hover">
                        <h3 class="experiment-text">{{this.experimentDetail.experimentquestion}}</h3>
                    </el-card>
                </el-col>   
                <el-col :span="22">
                    <h1 class="experiment-title"></h1>
                    <el-card shadow="hover">
                        <el-input
                        type="textarea"
                        :rows="6"
                        placeholder="(400)"
                        v-model="reportInfo.experimentperception">
                        </el-input>
                    </el-card>
                </el-col> 
                <el-col :span="22">
                    <br/>
                    <el-button 
                    type="primary" 
                    plain 
                    class="experiment-button"
                    @click="submitExperimentReport"
                    ></el-button>    
                </el-col> 
            </el-row>       
        </div>
    </div>
</template>

<script>
export default {
    //
    name: "FinishReport",
    //
    data(){

        return{
            //
            experimentSubmit:false,
            //
            experimentNo:0,
            //
            experimentDetail:{},
            //
            reportInfo:{
                experimentno:0,
                userid:0,
                username:"student",
                experimentphenomenon:"",
                experimentperception:"",
                experimentgrade:0,
                experimentcomments:"",
                experimentsubmissontime:"",
                experimentreportno:0,
                experimentname:""
            }
        }
    },
    //
    methods: {
        
         //
         getExperimentDetail(){
            //
            let data = {
                method: "get",
                url: "/experiment/getexperiment",
                params: {"experimentNo": this.experimentNo}
            }
            //
            this.$axios.request(data)
            .then(response => {
                //
               if(200 == response.data.code){
                //
                   this.experimentDetail = response.data.result;
               }
               else{
                    this.$message.error("");
               }
            })
            .catch(failResponse => {
                console.log(failResponse)
                this.$message.error("");
            })
        },

        //
        submitExperimentReport(){
            //
            if(this.experimentSubmit){
                this.$message.error("")
                return
            }
            //,
            if("" == this.reportInfo.experimentphenomenon){
                this.$message.error("")
                return
            }
            else if("" == this.reportInfo.experimentperception){
                this.$message.error("")
                return
            }
            //
            let nowDate = new Date()
            let year = nowDate.getFullYear()
            let month = nowDate.getMonth() + 1
            let day = nowDate.getDate()
            this.reportInfo.experimentsubmissontime =  year + "-" + month + "-" + day
            console.log(this.reportInfo.experimentsubmissontime)
            //
            let data = {
                    method: "post",
                    url:"/experiment/submitexperimentreport",
                    data: this.reportInfo
                }
            //
            this.$axios.request(data)
            .then(response => {
                //
               if(200 == response.data.code){
                    this.$message.success("")
                    this.experimentSubmit = true;
               }
               else{
                    this.$message.error("");
               }
            })
            .catch(failResponse => {
                console.log(failResponse)
                this.$message.error("");
            })
        }
    },
    //
    mounted(){
        //
        this.experimentNo = this.$route.query.experimentno
        this.reportInfo.experimentno = this.$route.query.experimentno
        //
        this.reportInfo.experimentname = this.$route.query.experimentname
        //
        this.reportInfo.userid = this.$route.query.userId
        //
        this.reportInfo.username = this.$store.getters.username
        //
        this.getExperimentDetail()
    }
}
</script>

<style scoped>
.experiment-title{
    font-family:"PingFang SC";
    font-size:25px;
    color:black;
    text-align:left
}

.experiment-div{
    width: 100%;
    height: 420px;
}

.experiment-text{
    font-family:"PingFang SC";
    font-size:15px;
    color:black;
    text-align:left;
    width: 95%;
}

.experiment-button{
    position:relative;
    left: -44.4%;
}
</style>


