<template>
    <div>
        <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/myexercise'}"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <br/>

        <div v-for="each in this.tableData" :key="tableData.indexOf(each)">
            <el-card shadow="hover">
                <h3 class="experiment-text">{{each.problemdescription}}</h3>
                <br/>
                <h3 class="experiment-text">A: {{each.problemoptiona}}</h3>
                <h3 class="experiment-text">B: {{each.problemoptionb}}</h3>
                <h3 class="experiment-text">C: {{each.problemoptionc}}</h3>
                <h3 class="experiment-text">D: {{each.problemoptiond}}</h3>
                <br/>
                <el-select 
                v-model="problemAnswer[tableData.indexOf(each)].Ans" 
                placeholder=""
                class="problem-select">
                    <el-option
                      v-for="item in options"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                    </el-option>
                </el-select>
            </el-card>
            <br/>
        </div>

        <el-button 
        type="warning"
        plain 
        @click="SubmitExercise"
        ></el-button>    

        <el-dialog
        title=""
        :visible.sync="dialogVisible"
        width="30%">
            <span></span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false"> </el-button>
                <el-button type="primary" @click="handleSubmit"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
export default {
    //
    name: "ComeExercise",
    //
    data(){

        return{
            //
            tableData: [],
            //
            exerciseNo: 0,
            //Id
            userId:0,
            //
            exerciseResult:{
                userid:0,
                exerciseno:0,
                exerciseusetime:0,
                exercisegrade:0
            },
            //
            ifSubmit:false,
            //
            startTime:0,
            //
            options: [{
            value: 'A',
            label: 'A'
            }, 
            {
            value: 'B',
            label: 'B'
            }, 
            {
            value: 'C',
            label: 'C'
            }, 
            {
            value: 'D',
            label: 'D'
            }],
            //
            problemAnswer:[],
            //
            dialogVisible: false,
            //
            endTime:""
        }
    },
    //
    methods:{

        //
        SubmitExercise(){
            //
            if((new Date) > (Date.parse(this.endTime))){
                this.$message.error("")
                return
            }
            //
            let each = 0
            for(each in this.problemAnswer){
                if(this.problemAnswer[each].Ans == ""){
                    let ansno = parseInt(each) + 1
                    let errmessage = "" + ansno + ""
                    this.$message.error(errmessage)
                    return
                }
            }
            //
            if(this.ifSubmit){
                this.$message.error("")
                return
            }
            this.dialogVisible = true;
        },

        //
        checkAns(){
            //
            let each = 0
            this.exerciseResult.exercisegrade = 0
            //
            for(each in this.problemAnswer){
                //
                if(this.problemAnswer[each].Ans == this.tableData[each].problemanswer){
                    //
                    this.exerciseResult.exercisegrade += 1
                }
            }
        },

        //
        handleSubmit(){
            this.dialogVisible = false;
            //
            let useTime = parseInt(((new Date()) - this.startTime) / 1000)
            this.exerciseResult.exerciseusetime = useTime
            //
            this.checkAns()
            //Id
            this.exerciseResult.userid = this.userId
            //
            this.exerciseResult.exerciseno = this.exerciseNo
            //
            let data = {
                method: "post",
                url: "/exercise/submitexercise",
                data:this.exerciseResult
            }
            //
            this.$axios.request(data)
            .then(response => {
                    //
                if(200 == response.data.code){
                    let sumessage = "" + this.exerciseResult.exerciseusetime + ""
                    this.$message.success(sumessage)
                    this.ifSubmit = true
                }
                else{
                    this.$message.error( response.data.result);
                }
            })
            .catch(failResponse => {
                console.log(failResponse)
                this.$message.error("");
            })
        },

        //
        getProblemList(){
            //
            let data = {
                method: "get",
                url: "/exercise/getexerciseproblemlist",
                params: {"exerciseNo": this.exerciseNo}
            }
            //
            this.$axios.request(data)
            .then(response => {
                    //
                if(200 == response.data.code){
                    //
                    this.tableData = response.data.result
                    this.saveAnswer(this.tableData)
                    //
                    this.$message.success("")
                    this.startTime = new Date()
                }
                else{
                    this.$message.error("");
                }
            })
            .catch(failResponse => {
                console.log(failResponse)
                this.$message.error("");
            })
        },

        //
        saveAnswer(tableData){
            let each = 0
            for(each in tableData){
                this.problemAnswer.push({"Ans":""})
            }
        },
    },
    //
    mounted(){
        //
        this.exerciseNo = this.$route.query.exerciseno
        //Id
        this.userId = this.$route.query.userId
        //
        this.endTime = this.$route.query.exerciseendtime
        //
        this.getProblemList()
    }
}
</script>

<style scoped>
.experiment-text{
    font-family:"PingFang SC";
    font-size:15px;
    color:black;
    text-align:left;
    width: 95%;
}
</style>


