<template>
    <div>
        <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/problemmanagement'}"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>

        <br/>
        <el-table
            :data="tableData.filter(data => !search || data.problemdescription.toLowerCase().includes(search.toLowerCase()))"
            style="width: 100%"
            height="400"
            @selection-change="handleSelectionChange">

               <!---->
            <el-table-column type="expand">
                <template slot-scope="props">
                    <el-form label-position="right" inline class="demo-table-expand">
                        <el-form-item label="">
                            <span>{{ props.row.problemno }}</span>
                        </el-form-item>
                        <el-form-item label="">
                            <span>{{ props.row.experimentno }}</span>
                        </el-form-item>
                        <el-form-item label="">
                            <span>{{ props.row.experimentname }}</span>
                        </el-form-item>
                        <el-form-item label="">
                            <span>{{ props.row.problemdescription }}</span>
                            </el-form-item>
                        <el-form-item label="A">
                            <span>{{ props.row.problemoptiona }}</span>
                        </el-form-item>
                        <el-form-item label="B">
                            <span>{{ props.row.problemoptionb }}</span>
                        </el-form-item>
                        <el-form-item label="C">
                            <span>{{ props.row.problemoptionc }}</span>
                        </el-form-item>
                        <el-form-item label="D">
                            <span>{{ props.row.problemoptiond }}</span>
                        </el-form-item>
                        <el-form-item label="">
                            <span>{{ props.row.problemanswer }}</span>
                        </el-form-item>
                    </el-form>
                </template>
            </el-table-column>

            <el-table-column
            label=""
            prop="problemno">
            </el-table-column>

            <el-table-column
            label=""
            prop="experimentname">
            </el-table-column>

            <el-table-column
            label=""
            prop="problemdescription">
            </el-table-column>

            <el-table-column
            type="selection"
            width="55">
            </el-table-column>

            <el-table-column
            align="right">
            <template slot="header">
                <el-input
                v-model="search"
                size="mini"
                placeholder=""/>
            </template>
            </el-table-column>
        </el-table>

        <!---->
        <br/>
        <el-form ref="form" :model="form" label-width="80px">    
            <el-form-item label="">
              <el-col :span="3">
                <el-date-picker 
                type="date" 
                value-format="yyyy-MM-dd"
                placeholder="" 
                v-model="form.date1" 
                style="width: 100%;"></el-date-picker>
              </el-col>
              <el-col class="line" :span="1">-</el-col>
              <el-col :span="3">
                <el-time-picker 
                value-format="HH:mm:ss" 
                placeholder="" 
                v-model="form.date2" 
                style="width: 100%;"></el-time-picker>
              </el-col>
              <el-col class="line" :span="2">--</el-col>
              <el-col :span="3">
                <el-date-picker 
                type="date"
                value-format="yyyy-MM-dd" 
                placeholder="" 
                v-model="form.date3" 
                style="width: 100%;"></el-date-picker>
              </el-col>
              <el-col class="line" :span="1">-</el-col>
              <el-col :span="3">
                <el-time-picker 
                placeholder="" 
                value-format="HH:mm:ss" 
                v-model="form.date4" 
                style="width: 100%;"></el-time-picker>
              </el-col>
            </el-form-item>
          </el-form>

        <br/>
        <!---->
        <el-button 
        type="primary" 
        plain 
        class="experiment-button"
        @click="publishExercise"
        ></el-button>
    </div>

    
</template>

<script>
export default {
    //
    name: "ExperimentExercise",
    //
    data(){

        return{
            //
            classInfo:{},
            //
            experimentNo:0,
            //
            search: '',
            //
            tableData: [],
            //Id
            userId:this.$store.getters.userId,
            //
            problemInfo: [],
           
            //
            exerciseInfo:{
                //
                exerciseno:0,
                //
                exercisestarttime:"",
                //
                exerciseendtime:"",
                //
                usersNo:[],
                //
                problemNo:[]
            },

            //
            form: {
                date1: '',
                date2: '',        
                date3: '',
                date4: '',  
            }
        }
    },
    //
    methods: {
        //
        DataManagement(){
            //
            this.exerciseInfo.exercisestarttime = this.form.date1 + " " + this.form.date2
            this.exerciseInfo.exerciseendtime = this.form.date3 + " " + this.form.date4
            let each = 0
            //
            for(each in this.problemInfo){
                this.exerciseInfo.problemNo.push(this.problemInfo[each].problemno)
            }
        },

        //
        publishExercise(){
            //
            if(0 == this.problemInfo.length){
                this.$message.error("")
                return
            }
            //
            else if(("" == this.form.date1) || ("" == this.form.date2) || ("" == this.form.date3) || ("" == this.form.date4)){
                this.$message.error("")
                return
            }
            //
            this.DataManagement()
            //
            let data = {
                    method: "post",
                    url:"/exercise/publishexercise",
                    data: this.exerciseInfo
                }
            //
            this.$axios.request(data)
            .then(response => {
                //
                if(200 == response.data.code){
                    //
                    this.$message.success("");
                }
                else{
                    this.$message.error("");
                }
            })
            .catch(failResponse => {
                console.log(failResponse)
                this.$message.error("");
            })
        },

        //
        handleSelectionChange(val){
            this.problemInfo = val
        },

        //
        getProblemList(){
            //
            let data = {
                method: "get",
                url: "/exercise/getexperimentproblemlist",
                params: {"experimentNo": this.experimentNo}
            }
            //
            this.$axios.request(data)
            .then(response => {
                    //
                if(200 == response.data.code){
                    //
                    this.tableData = response.data.result
                }
                else{
                    this.$message.error("");
                }
            })
            .catch(failResponse => {
                console.log(failResponse)
                this.$message.error("");
            })
        },

        //
        getClassInfo(){
             //
             let data = {
                method: "get",
                url: "/class/getexperimentclass",
                params: {"userId": this.userId}
            }
            //
            this.$axios.request(data)
            .then(response => {
                    //
                if(200 == response.data.code){
                    //
                    this.classInfo = response.data.result
                    //
                    this.exerciseInfo.usersNo = this.classInfo.usersNo
                }
                else{
                    this.$message.error("");
                }
            })
            .catch(failResponse => {
                console.log(failResponse)
                this.$message.error("");
            })
        }
    },
    //
    mounted(){
        //
        this.experimentNo = this.$route.query.experimentno
        //
        this.getProblemList()
        //
        this.getClassInfo()
    }
}
</script>

<style scoped>
.demo-table-expand {
    font-size: 0;
  }
.demo-table-expand label {
width: 90px;
color: #99a9bf;
}
.demo-table-expand .el-form-item {
margin-right: 50px;
margin-bottom: 0;
width: 100%;
height: 100%;
}
.experiment-button{
    position:relative;
    left: -46%;
}
</style>


