<template>
    <Contentfield>
        <div>
          <el-breadcrumb separator="/">
            <el-breadcrumb-item></el-breadcrumb-item>
            <!-- <el-breadcrumb-item :to="{ path: '/experimentdetail' }"></el-breadcrumb-item> -->
          </el-breadcrumb>
        </div>
       
        <br/>
        <el-table
            :data="tableData.filter(data => !search || data.experimentname.toLowerCase().includes(search.toLowerCase()))"
            style="width: 100%"
            height="380">
            <el-table-column
            label=""
            prop="experimentno">
            </el-table-column>
            <el-table-column
            label=""
            prop="experimentname">
            </el-table-column>
            <el-table-column
            align="right">
            <template slot="header">
                <el-input
                v-model="search"
                size="mini"
                placeholder=""/>
            </template>
            <template slot-scope="scope">
                <el-button
                size="mini"
                @click="handleEdit(scope.row)"></el-button>

                <el-button
                size="mini"
                type="danger"
                @click="handleClickDelete(scope.row)"></el-button>

            </template>
            </el-table-column>
        </el-table>
        <br/>
        <!---->
        <el-button 
        type="primary" 
        plain 
        class="experiment-button"
        @click="comeToCreateExperimentProject"
        ></el-button>

        <el-dialog
        title=""
        :visible.sync="dialogVisible"
        width="30%">
            <span></span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false"> </el-button>
                <el-button type="primary" @click="handleDelete"> </el-button>
            </span>
        </el-dialog>
    </Contentfield>
</template>

<script>
import Contentfield from "@/components/AdminCom/Contentfield.vue";
export default {
    //
    components: { Contentfield,},
    //
    name:"ExperimentProjectManagement",
    //
    data(){
        return{
            //
            tableData: [],
            //
            search: '',
            //
            dialogVisible: false,
            //
            deleteInfo:{}
        }
    },
    //
    methods:{

        //
        handleEdit(row) {
            row["path"] = "/experimentprojectmanagement"
            this.$router.push({path:'/experimentdetail',query:row})
        },

        //
        handleClickDelete(row){
            this.dialogVisible = true;
            this.deleteInfo = row;
        },

        //
        handleDelete() {
            this.dialogVisible = false
            this.deleteExperimentProject(this.deleteInfo.experimentno)
        },

        //
        deleteExperimentProject(experimentNo){
            //
            let data = {
                method: "post",
                url: "/experiment/deleteaexperiment",
                params: {"experimentNo": experimentNo}
            }
            //
            this.$axios.request(data)
            .then(response => {
                    //
                if(200 == response.data.code){
                    //
                    this.$message.success("");
                }
                else{
                    this.$message.error("");
                }
            })
            .catch(failResponse => {
                console.log(failResponse)
                this.$message.error("");
            })
        },

        //
        getExperimentList() {
            //
            this.$axios.get('/experiment/getexperimentlist')
            .then(response => {
                //
               if(200 == response.data.code){
                    this.tableData = response.data.result
               }
               else{
                    this.$message.error("");
               }
            })
            .catch(failResponse => {
                this.$message.error("");
            })
        },

        //
        comeToCreateExperimentProject(){
            this.$router.push({path:"/createexperimentproject"})
        }

    },
    //
    mounted(){
        //
        this.getExperimentList()
    },
}
</script>

<style scoped>
.experiment-button{
    position:absolute;
    left: 18.2%;
}
</style>


