<template>
    <Contentfield>
        <el-breadcrumb separator="/">
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>

        <br/>
        <el-table
            :data="tableData.filter(data => !search || data.exercisestarttime.toLowerCase().includes(search.toLowerCase()))"
            style="width: 100%"
            height="380">
            <el-table-column
            label=""
            prop="exerciseno">
            </el-table-column>

            <el-table-column
            label=""
            prop="exercisestarttime">
            </el-table-column>

            <el-table-column
            label=""
            prop="exerciseendtime">
            </el-table-column>

            <el-table-column
            align="right">
            <template slot="header">
                <el-input
                v-model="search"
                size="mini"
                placeholder=""/>
            </template>
            <template slot-scope="scope">
                <el-button
                size="mini"
                @click="handleEdit(scope.row)"></el-button>

                <el-button
                size="mini"
                type="success"
                @click="comeToExercise(scope.row)"></el-button>
            </template>
            </el-table-column>
        </el-table>
    </Contentfield>
</template>

<script>
import Contentfield from "@/components/AdminCom/Contentfield.vue";
export default {
    //
    components: { Contentfield,},
    //
    name: "MyExercise",
    //
    data(){

        return{
            //
            tableData: [],
            //
            search: '',
            //Id
            userId: this.$store.getters.userId
        }
    },
    //
    methods: {
        
        //
        comeToExercise(row){
            if((Date.parse(row.exerciseendtime)) < new Date()){
                this.$message.error("")
                return
            }
            if(new Date() < (Date.parse(row.exercisestarttime))){
                this.$message.error("")
                return
            }
            //Id
            row["userId"] = this.userId
            this.$router.push({path:'/comeexercise',query:row})
        },

        //
        handleEdit(row){
            if((Date.parse(row.exerciseendtime)) > new Date()){
                this.$message.error("")
                return
            }
            this.$router.push({path:'/exercisedetail',query:row})
        },

        //
        getExerciseList(){
            //
            let data = {
                method: "get",
                url: "/exercise/getexerciselist",
                params: {"userId": this.userId}
            }
            //
            this.$axios.request(data)
            .then(response => {
                    //
                if(200 == response.data.code){
                    //
                    this.tableData = response.data.result
                }
                else{
                    this.$message.error("");
                }
            })
            .catch(failResponse => {
                console.log(failResponse)
                this.$message.error("");
            })
        }
    },
    //
    mounted(){
        //
        this.getExerciseList()
    }
}
</script>

<style>

</style>


