<template>
    <div>
        <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/experimentprojectmanagement'}"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>

        <br/>
        <div class="experiment-div">
            
            <h1 class="experiment-title">
                
            </h1>
            <el-input
            placeholder=""
            v-model="experimentInfo.experimentname"
            clearable
            class="experiment-text">
            </el-input>        
            <el-row :gutter="12">  
                <el-col :span="22">
                    <h1 class="experiment-title"></h1>
                    <el-input
                    type="textarea"
                    :rows="6"
                    placeholder=""
                    v-model="experimentInfo.experimentpurpose">
                    </el-input>
                </el-col>             
                <el-col :span="22">
                    <h1 class="experiment-title"></h1>
                    <el-input
                    type="textarea"
                    :rows="6"
                    placeholder=""
                    v-model="experimentInfo.experimentbasicknowledge">
                    </el-input>
                </el-col>   
                <el-col :span="22">
                    <h1 class="experiment-title"></h1>
                    <el-input
                    type="textarea"
                    :rows="6"
                    placeholder=""
                    v-model="experimentInfo.experimentsteps">
                    </el-input>
                </el-col>   
                <el-col :span="22">
                    <h1 class="experiment-title"></h1>
                    <el-input
                    type="textarea"
                    :rows="6"
                    placeholder=""
                    v-model="experimentInfo.experimentquestion">
                    </el-input>
                </el-col>  
                <el-col :span="22">
                    <br/>
                    <el-button 
                    type="primary" 
                    plain 
                    class="experiment-button"
                    @click="submitExperimentProject"
                    ></el-button>    
                </el-col> 
            </el-row>                
        </div>
    </div> 
</template>

<script>
export default {
    //
    name:"CreateExperimentProject",
    //
    data(){

        return{
            experimentInfo:{
                experimentno:0,
                experimentname:"",
                experimentpurpose:"",
                experimentbasicknowledge:"",
                experimentsteps:"",
                experimentquestion:""
            }
        }
    },
    //
    methods: {
        //
        checkExperimentProject(){
            if(this.experimentInfo.experimentname == ""){
                this.$message.error("");
                return false
            }
            else if(this.experimentInfo.experimentpurpose == ""){
                this.$message.error("");
                return false
            }
            else if(this.experimentInfo.experimentbasicknowledge == ""){
                this.$message.error("");
                return false
            }
            else if(this.experimentInfo.experimentsteps == ""){
                this.$message.error("");
                return false
            }
            else if(this.experimentInfo.experimentquestion == ""){
                this.$message.error("");
                return false
            }
            else{
                return true
            }
        },

        //
        submitExperimentProject(){
             //
            if(this.checkExperimentProject()){

                //
                let data = {
                    method: "post",
                    url:"/experiment/createexperiment",
                    data: this.experimentInfo
                }
                //
                this.$axios.request(data)
                .then(response => {
                        //
                    if(200 == response.data.code){
                        //
                        this.$message.success("");
                    }
                    else{
                        this.$message.error("");
                    }
                })
                .catch(failResponse => {
                    console.log(failResponse)
                    this.$message.error("");
                })
            }
        }
    },
}
</script>

<style scoped>
.experiment-title{
    font-family:"PingFang SC";
    font-size:25px;
    color:black;
    text-align:left
}

.experiment-div{
    width: 100%;
    height: 420px;
}

.experiment-text{
    width: 91%;
    position:relative;
    left:-4.4%;
}

.experiment-button{
    position:relative;
    left: -44.4%;
}
</style>


