<template>
  <Contentfield>
    <div>
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/admin' }"></el-breadcrumb-item>
        <el-breadcrumb-item><a href="/admin/adminborrow"></a></el-breadcrumb-item>
      </el-breadcrumb>
      <el-table
          :data="tableData"
          style="width: 100%"
          :default-sort="{ prop: 'id', order: 'descending' }"
      >
        <el-table-column prop="id" label="id" sortable width="150">
        </el-table-column>
        <el-table-column prop="username" label="" width="120">
        </el-table-column>
        <el-table-column prop="mail" label=""  width="200">
        </el-table-column>
        <el-table-column prop="status" label="" width="150"> </el-table-column>
        <el-table-column
            prop="level"
            label=""
            width="150"
            :filters="[
            { text: '', value: '' },
            { text: '', value: '' },
            { text: '', value: '' },
            { text: '', value: '' },
            { text: '', value: '' },
            { text: '', value: '' },
            { text: '', value: '' },
          ]"
            :filter-method="filterTag"
            filter-placement="bottom-end"
        >
          <template slot-scope="scope">
            <el-tag
                :type="
                scope.row.level == ''
                  ? ''
                  : scope.row.level == ''
                  ? 'success'
                  : scope.row.level == ''
                  ? 'danger'
                  : scope.row.level == ''
                  ? 'success'
                  : scope.row.level == ''
                  ? 'warning'
                  : scope.row.level == ''
                  ? 'info'
                  : scope.row.level == ''
                  ? 'info'
                  : 'primary'
              "
                disable-transitions
            >{{ scope.row.level }}</el-tag
            >
          </template>
        </el-table-column>
        <!-- <el-table-column prop="status" label="" width="150">
          </el-table-column> -->
        <el-table-column label="" width="400px">

          <template slot-scope="scope">
            <el-button size="mini"
                       type="success"
                       @click="mod(scope.row)"
                       plain
            ></el-button
            >

            <el-dialog
                title=""
                :visible.sync="dialogFormVisible">
            <el-form :model="dataForm"
                     :label-width="formLabelWidth"
                     :rules="rules"
                     ref="dataForm">
              <el-form-item label="ID" prop="ID" v-show="false">
                <el-col :span="10">
                  <el-input v-model="dataForm.id" autocomplete="off" size="small"></el-input>
                </el-col>
              </el-form-item>
              <el-form-item label="" >

                <el-select v-model="dataForm.level" placeholder="">
                  <el-option
                      v-for="item in options"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                  </el-option>
                </el-select>
              </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
              <el-button @click="cancel"> </el-button>
              <el-button type="primary" @click="confirm"> </el-button>
            </div>
            </el-dialog>

            <el-button size="mini"
                       type="warning"
                       @click="mod(scope.row)"
                       plain
                       v-show="scope.row.level!=''"
            ></el-button
            >
            <el-button
                size="mini"
                @click="deleteuser(scope.$index, scope.row)"
            ></el-button>
            <el-button v-if="sending === ''"
                       type="warning"
                       size="mini"
                       @click="changestatus(scope.$index, scope.row); refresh()"
                       v-show="scope.row.level==''"></el-button>
            <el-button v-else size="mini"
                       type="danger"
                       @click="changestatus(scope.$index, scope.row); refresh()"
                       v-show="scope.row.level==''"></el-button>
          </template>

        </el-table-column>
      </el-table>
    </div>
  </Contentfield>
</template>

<script>
//import Bookcard from "@/components/MypageCom/MyBorrowCom/Bookcard.vue";
import Contentfield from "@/components/AdminCom/Contentfield.vue";
export default {
  components: { Contentfield,},
  data() {
    return {
      id: "",
      username: "",
      level: "",
      mail:"",
      status:"",
      tableData: [],
      sending:"",
      dialogVisible: false,
      options: [{
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      },{
        value: '',
        label: ''
      }],
      value: '',
      dialogFormVisible: false,
      dataForm: {
        level:'',
        id:'',
      }
    };
  },
  mounted() {
    this.getdata();
  },
  methods: {
    add(){
      this.dialogFormVisible = true;
    },
    confirm(){//
      var _this = this;
      this.$axios
          .post("/changelevel", {
            id: this.dataForm.id,
            level:this.dataForm.level})
          .then(successResponse => {
        if (successResponse.data.code === 200) {
          this.$message.success(""+successResponse.data.result.level);
          this.getdata();
        }
        else{
          this.$message.error("");
        }
      }).catch(failResponse => {
        this.$message.error("");
      })
      this.dialogFormVisible = false;
    },
    cancel(){//
      this.dialogFormVisible = false;
    },
    mod(row){
      //
      this.dataForm.id=row.id;
      //
      this.dialogFormVisible = true;
      this.modFlag = true;
    },
    changestatus(index, row) {
      this.$message("");
      //alert(this.regUser.regPhone)
      this.$axios({
        url: "/sms",
        method: "post",
        data: row.mail,
        headers: {
          "Content-Type": "text/plain",
        },
      }).then((res) => {
        // console.log(res.data.code)
        if (res.data != null) {
          this.$message.success("");
          this.findVerification = res.data;
        } else {
          console.log(res.data);
          this.$message.error("");
          return;
        }
      });
      this.sending=""
    },
    handleClose(done) {
      this.$confirm('')
          .then(_ => {
            done();
          })
          .catch(_ => {});
    },
    getdata() {
      var _this = this
      this.$axios
          .get('/userlist')
          .then(resp => {
            if (resp && resp.data.code === 200) {
              _this.tableData = resp.data.result
            }
          })
          .catch(failResponse => {
            this.$message.error("");
          })
    },

    deleteuser(index,row) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$axios
            .post('/delete', {id: row.id}).then(res => {

          if (res.data.code == "200") {
            this.$message({
              type: 'info',
              message: ''
            })
            this.getdata();
          }
          else {
            this.$message({
              type: 'info',
              message: ''
            })
          }
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },

    filterTag(value, row) {
      return row.status === value;
    },
  },
};
</script>

<style scoped>
.rwd-table {
  position: absolute;
  top: 150px;
  left: 300px;
}
.el-breadcrumb {
  height: 50px;
}
</style>



