import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
const state ={
    //username: '',
    username: '' || localStorage.getItem('username'),
    level: '' || localStorage.getItem('level'),
    //id
    userId: '' || localStorage.getItem('userId'),
    userState:0,
    menu_data:[],
    isLoadRoute:false,
    editableTabsValue: '1',
    editableTabs: [
        {
            title: '',
            name: '',
            content: ''
        },

    ]
}
const mutations ={
    setUser (state, name) {
        state.username = name
    },
    handleUserName: (state, username) => {
        state.username = username
        // localStoragevuex
        localStorage.setItem('username', username)
    },
    handleLevel: (state, level) => {
        state.level = level
        // localStoragevuex
        localStorage.setItem('username', level)
    },
    //ID
    handleUserId: (state, userId) =>{
        state.userId = userId
        localStorage.setItem('userId', userId)
    },
    setUserState (state, data) {
        state.userState +=data
    },
    addEditableTabs(state,tab){
        //tab.name  editableTabs 
        let idx = state.editableTabs.findIndex(item=>item.name===tab.name)
        if(idx===-1){//
            state.editableTabs.push({
                title: tab.name,
                name: tab.name
            })
        }
        state.editableTabsValue = tab.name
    },
    setMenuData(state,data){
        state.menu_data = data
    },

}


const getters = {
    getUserState (state) {
        let data;
        if(state.userState==0){
            data=''
        }else{
            data = state.userState+''
        }
        return data;
    },
    username: (state) => state.username,
    level: (state) => state.level,
    userId: (state) => state.userId
}
export default new Vuex.Store({
    state,
    mutations,
    getters,
})





