<template>
  <div>
    <el-breadcrumb separator="/">
      <el-breadcrumb-item ></el-breadcrumb-item>
      <el-breadcrumb-item><a href="/admin/adminborrow"></a></el-breadcrumb-item>
    </el-breadcrumb>
    <el-table
        :data="tableData"
        style="width: 100%"
        :default-sort="{ prop: 'date', order: 'descending' }"
    >
      <el-table-column
          prop="classno"
          label=""
          width="150"
          :filters="[
            { text: '5', value: 5 },
            { text: '6', value: 6 },
          ]"
          :filter-method="filterTag"
          filter-placement="bottom-end"
      >

      </el-table-column>
      <el-table-column prop="userid" label="id" width="120">
      </el-table-column>
      <el-table-column prop="attandancegrade" label=""  width="200">
      </el-table-column>
      <el-table-column prop="experimentgrade" label="" width="150"> </el-table-column>
      <el-table-column prop="totalgrade" label="" width="150"> </el-table-column>
      <!-- <el-table-column prop="status" label="" width="150">
        </el-table-column> -->
    </el-table>
  </div>
</template>

<script>
export default {
  name: "GradeDisplay",
  data() {
    return {
      classno: "",
      attandancenum: "",
      attandanceweight: "",
      experimentnum:"",
      experimentweight:"",
      tableData: [],
      sending:"",
      dialogVisible: false,
      value: '',
      dialogFormVisible: false,
      dataForm: {
        classno:'',
        attandanceweight:'',
        attandancenum:'',
        experimentnum:'',
        experimentweight:'',
      }
    };
  },
  mounted() {
    this.getdata();
  },
  methods: {
    add(){
      this.dialogFormVisible = true;
    },
    confirm(){//
      var _this = this;
      this.$axios
          .post("/changegradeset", {
            classno: this.dataForm.classno,
            attandanceweight:this.dataForm.attandanceweight,
            attandancenum:this.dataForm. attandancenum,
            experimentnum:this.dataForm.experimentnum,
            experimentweight:this.dataForm.experimentweight
          })
          .then(successResponse => {
            if (successResponse.data.code === 200) {
              this.$message.success(""+successResponse.data.result.classno);
              this.getdata();
            }
            else{
              this.$message.error("");
            }
          }).catch(failResponse => {
        this.$message.error(""+this.dataForm.classno);
      })
      this.dialogFormVisible = false;
    },
    cancel(){//
      this.dialogFormVisible = false;
    },
    mod(row){
      //
      this.dataForm.classno=row.classno;
      //
      this.dialogFormVisible = true;
      this.modFlag = true;
    },
    handleClose(done) {
      this.$confirm('')
          .then(_ => {
            done();
          })
          .catch(_ => {});
    },
    getdata() {
      var _this = this
      this.$axios
          .get('/gradelist')
          .then(resp => {
            if (resp && resp.data.code === 200) {
              _this.tableData = resp.data.result
            }
          })
          .catch(failResponse => {
            this.$message.error("");
          })
    },

    filterTag(value, row) {
      return row.classno === value;
    },
  },

}
</script>

<style scoped>
.rwd-table {
  position: absolute;
  top: 150px;
  left: 300px;
}
.el-breadcrumb {
  height: 50px;
}
</style>



