<template>
    <div>
        <canvas id="QRCode_header" style="width: 280px; height: 280px"></canvas>
    </div>
</template>

<script>
import QRCode from "qrcode"; //
export default {
  props: {
    uuurl: String,
  },
  data() {
    return {
      qrUrl: "",
    };
  },
  watch: {},
  mounted() {
    this.getQRCode();
  },
  created() {},
  methods: {
    getQRCode() {
      let _this = this;
      //URLjs
      this.qrUrl = "https://152.136.245.230:7784/play/index.html?uid=1&zid=1&roomid=53";
      let opts = {
        errorCorrectionLevel: "H", //
        type: "image/png", //
        quality: 0.3, //
        margin: 0, //
        width: 280, //
        height: 280, //
        text: "http://www.xxx.com", //
        // text: _this.uuurl, //
        color: {
          dark: "#333333", //
          light: "#fff", //
        },
      };

      let msg = document.getElementById("QRCode_header");
      // valmsgcanvas
      QRCode.toCanvas(msg, this.qrUrl, opts, function (error) {
        if (error) {
          console.log("", error);
          this.$message.error("");
        }
      });
    },
  },
};
</script>



