<template>
    <Contentfield>
        <div>
            <el-breadcrumb separator="/">
            <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <br/>

        <el-table
        :data="tableData.filter(data => !search || data.experimentname.toLowerCase().includes(search.toLowerCase()))"
        style="width: 100%"
        height="380">

            <el-table-column
            label=""
            prop="experimentno">
            </el-table-column>

            <el-table-column
            label=""
            prop="experimentname">
            </el-table-column>

            <el-table-column
            align="right">
            <template slot="header">
                <el-input
                v-model="search"
                size="mini"
                placeholder=""/>
            </template>
            <template slot-scope="scope">
                <el-button
                size="mini"
                @click="handleEdit(scope.row)"></el-button>
            </template>
            </el-table-column>
        </el-table>
    </Contentfield>
</template>

<script>
import Contentfield from "@/components/AdminCom/Contentfield.vue";
export default {

    //
    components: { Contentfield,},

    //
    name: "ManageFile",

    //
    data(){

        return{
            //
            tableData: [],
            //
            search: '',
            //
            experimentList:[]
        }
    },

    //
    methods: {
        
        //
        getSimpleList(){
            let each = 0
            for(each in this.tableData){
                let data = {
                    "value":each,
                    "label":this.tableData[each]["experimentname"]
                }
                this.experimentList.push(data)
            }
        },

        //
        handleEdit(row) {
            //
            this.getSimpleList()
            //
            row["list"] = this.experimentList
            this.$router.push({path:'/manageexperimentfile',query:row})
        },

        //
        getexperimentList(){
            //
            let data = {
                method: "get",
                url: "/experiment/getexperimentlist",
            }
            //
            this.$axios.request(data)
            .then(response => {
                    //
                if(200 == response.data.code){
                    //
                    this.tableData = response.data.result
                }
                else{
                    this.$message.error("");
                }
            })
            .catch(failResponse => {
                console.log(failResponse)
                this.$message.error("");
            })
        }
    },

    //
    mounted() {
        //
        this.getexperimentList()
    },
}
</script>

<style>

</style>


