<template>
    <Contentfield>
        <div>
            <el-breadcrumb separator="/">
              <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <br/>

        <h1 style="text-align:left">{{this.experimentClassInfo.experimentclassname}}</h1>
        <h1 style="text-align:left">{{this.experimentClassInfo.classstarttime}}</h1>
        <h1 style="text-align:left">{{this.experimentClassNo}}</h1>

        <h1 style="text-align:left"></h1>
        <el-table
            :data="classExperimentTable.filter(data => !classSearch || data.experimentname.toLowerCase().includes(classSearch.toLowerCase()))"
            style="width: 100%"
            height="380">
            <el-table-column
            label=""
            prop="experimentno">
            </el-table-column>
            <el-table-column
            label=""
            prop="experimentname">
            </el-table-column>
            <el-table-column
            align="right">
            <template slot="header">
                <el-input
                v-model="classSearch"
                size="mini"
                placeholder=""/>
            </template>
            <template slot-scope="scope">
                <el-button
                size="mini"
                @click="handleCheck(scope.row)"></el-button>
            </template>
            </el-table-column>
        </el-table>

        <h1 style="text-align:left"></h1>
        <el-table
        :data="classUserTable.filter(data => !classSearch || data.username.toLowerCase().includes(classSearch.toLowerCase()))"
        style="width: 100%"
        height="400">

            <el-table-column
            label="ID"
            prop="userid">
            </el-table-column>

            <el-table-column
            label=""
            prop="username">
            </el-table-column>

            <el-table-column
            prop="level"
            label=""
            width="150"
            :filters="[
            { text: '', value: '' },
            { text: '', value: '' },]"
            filter-placement="bottom-end">
                <template slot-scope="scope">
                    <el-tag
                        :type="
                        scope.row.level == ''
                            ? 'warning'
                            : scope.row.level == ''
                            ? 'success'
                            : 'primary'"
                        disable-transitions
                    >{{ scope.row.level }}</el-tag>
                </template>
            </el-table-column>

            <el-table-column
            label=""
            prop="status">
            </el-table-column>

            <el-table-column
            align="right">
                <template slot="header">
                    <el-input
                    v-model="classSearch"
                    size="mini"
                    placeholder=""/>
                </template>
            </el-table-column>
        </el-table>
    </Contentfield>
</template>

<script>
import Contentfield from "@/components/AdminCom/Contentfield.vue";
export default {
    //
    components: { Contentfield,},
    //
    name: "MyClass",
    //
    data(){

        return{
            //Id
            userId:this.$store.getters.userId,
            //
            experimentClassNo:0,
            //
            experimentClassInfo:{},
            //
            classExperimentTable:[],
            //
            classSearch:"",
            //
            classUserTable:[],
            //
            classSearch:""
        }
    },
    //
    methods:{

        //
        handleCheck(row){
            row["path"] = "/myclass"
            this.$router.push({path:'/experimentdetail',query:row})
        },

        //
        getExperimentClass(){
            //
            let data = {
                method: "get",
                url: "/class/findexperimentclass",
                params: {"userId": this.userId}
            }
            //
            this.$axios.request(data)
            .then(response => {
                //
               if(200 == response.data.code){
                //
                   this.experimentClassNo = response.data.result;
               }
               else{
                    this.$message.error("");
               }
            })
            .catch(failResponse => {
                console.log(failResponse)
                this.$message.error("");
            })
        },

        //
        getExperimentClassInfo(){
            //
            let data = {
                method: "get",
                url: "/class/getexperimentclass",
                params: {"userId": this.userId}
            }
            //
            this.$axios.request(data)
            .then(response => {
                //
               if(200 == response.data.code){
                //
                   this.experimentClassInfo = response.data.result;
               }
               else{
                    this.$message.error("");
               }
            })
            .catch(failResponse => {
                console.log(failResponse)
                this.$message.error("");
            })
        },

        //
        getClassExperimentList(){
            //
            let data = {
                method: "get",
                url: "/experiment/getallexperimentinclass",
                params: {"userId": this.userId}
            }
            //
            this.$axios.request(data)
            .then(response => {
                //
               if(200 == response.data.code){
                //
                   this.classExperimentTable = response.data.result;
               }
               else{
                    this.$message.error("");
               }
            })
            .catch(failResponse => {
                console.log(failResponse)
                this.$message.error("");
            })
        },

         //
         getClassUserList(){
            //
            let data = {
                method: "get",
                url: "/experiment/getallstudentinclass",
                params: {"userId": this.userId}
            }
            //
            this.$axios.request(data)
            .then(response => {
                //
               if(200 == response.data.code){
                //
                   this.classUserTable = response.data.result;
               }
               else{
                    this.$message.error("");
               }
            })
            .catch(failResponse => {
                console.log(failResponse)
                this.$message.error("");
            })
        },
    },
    //
    mounted(){
        //
        this.getExperimentClass()
        //
        this.getExperimentClassInfo()
        //
        this.getClassExperimentList()
        //
        this.getClassUserList()
    }
}
</script>

<style>

</style>


