<template>
    <div>
        <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/myexercise'}"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <br/>

        <el-table
            :data="tableData.filter(data => !search || data.problemdescription.toLowerCase().includes(search.toLowerCase()))"
            style="width: 100%"
            height="430">

               <!---->
            <el-table-column type="expand">
                <template slot-scope="props">
                    <el-form label-position="right" inline class="demo-table-expand">
                        <el-form-item label="">
                            <span>{{ props.row.problemno }}</span>
                        </el-form-item>
                        <el-form-item label="">
                            <span>{{ props.row.experimentno }}</span>
                        </el-form-item>
                        <el-form-item label="">
                            <span>{{ props.row.experimentname }}</span>
                        </el-form-item>
                        <el-form-item label="">
                            <span>{{ props.row.problemdescription }}</span>
                            </el-form-item>
                        <el-form-item label="A">
                            <span>{{ props.row.problemoptiona }}</span>
                        </el-form-item>
                        <el-form-item label="B">
                            <span>{{ props.row.problemoptionb }}</span>
                        </el-form-item>
                        <el-form-item label="C">
                            <span>{{ props.row.problemoptionc }}</span>
                        </el-form-item>
                        <el-form-item label="D">
                            <span>{{ props.row.problemoptiond }}</span>
                        </el-form-item>
                        <el-form-item label="">
                            <span>{{ props.row.problemanswer }}</span>
                        </el-form-item>
                    </el-form>
                </template>
            </el-table-column>

            <el-table-column
            label=""
            prop="problemno">
            </el-table-column>

            <el-table-column
            label=""
            prop="experimentname">
            </el-table-column>

            <el-table-column
            label=""
            prop="problemdescription">
            </el-table-column>

            <el-table-column
            align="right">
            <template slot="header">
                <el-input
                v-model="search"
                size="mini"
                placeholder=""/>
            </template>
            </el-table-column>
        </el-table>
        <br/>
        <h1 class="experiment-title">
            {{this.exerciseResult.exercisegrade}} {{this.exerciseResult.exerciseusetime}}
        </h1>
    </div>
</template>

<script>
export default {
    //
    name: "ExerciseDetail",
    //
    data(){

        return{
            //
            tableData: [],
            //
            search: '',
            //
            exerciseNo: 0,
            //Id
            userId:this.$store.getters.userId,
            //
            exerciseResult:{}
        }
    },
    //
    methods:{

         //
         getProblemList(){
            //
            let data = {
                method: "get",
                url: "/exercise/getexerciseproblemlist",
                params: {"exerciseNo": this.exerciseNo}
            }
            //
            this.$axios.request(data)
            .then(response => {
                    //
                if(200 == response.data.code){
                    //
                    this.tableData = response.data.result
                }
                else{
                    this.$message.error("");
                }
            })
            .catch(failResponse => {
                console.log(failResponse)
                this.$message.error("");
            })
        },

        //
        getExerciseResult(){
            //
            let data = {
                method: "get",
                url: "/exercise/getexerciseresult",
                params: {
                    "userId": this.userId,
                    "exerciseNo": this.exerciseNo
                }
            }
            //
            this.$axios.request(data)
            .then(response => {
                    //
                if(200 == response.data.code){
                    //
                    this.exerciseResult = response.data.result
                }
                else{
                    this.$message.error("");
                }
            })
            .catch(failResponse => {
                console.log(failResponse)
                this.$message.error("");
            })
        }
    },
    //
    mounted(){
        //
        this.exerciseNo = this.$route.query.exerciseno
        //
        this.getProblemList()
        //
        this.getExerciseResult()
    }
}
</script>

<style scoped>
.demo-table-expand {
    font-size: 0;
  }
.demo-table-expand label {
width: 90px;
color: #99a9bf;
}
.demo-table-expand .el-form-item {
margin-right: 50px;
margin-bottom: 0;
width: 100%;
height: 100%;
}
.experiment-title{
    font-family:"PingFang SC";
    font-size:25px;
    color:black;
    text-align:left
}
</style>


