<template>
    <Contentfield>
        <div>
            <el-breadcrumb separator="/">
              <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
        </div>

        <br/>
        <el-table
            :data="tableData.filter(data => !search || data.experimentclassname.toLowerCase().includes(search.toLowerCase()))"
            style="width: 100%"
            height="380">

            <el-table-column
            label=""
            prop="experimentclassno">
            </el-table-column>

            <el-table-column
            label=""
            prop="experimentclassname">
            </el-table-column>

            <el-table-column
            label=""
            prop="classstarttime">
            </el-table-column>

            <el-table-column
            label="Id"
            prop="teacheridnumber">
            </el-table-column>

            <el-table-column
            align="right">
            <template slot="header">
                <el-input
                v-model="search"
                size="mini"
                placeholder=""/>
            </template>
            <template slot-scope="scope">

                <el-button
                size="mini"
                type="danger"
                @click="handleClickDelete(scope.row)"></el-button>

            </template>
            </el-table-column>
        </el-table>

        <br/>
        <!---->
        <el-button 
        type="primary" 
        plain 
        class="experiment-button"
        @click="comeToCreateClass"
        ></el-button>

        <el-dialog
        title=""
        :visible.sync="dialogVisible"
        width="30%">
            <span></span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false"> </el-button>
                <el-button type="primary" @click="handleDelete"> </el-button>
            </span>
        </el-dialog>
    </Contentfield>
</template>

<script>
import Contentfield from "@/components/AdminCom/Contentfield.vue";
export default {
    //
    components: { Contentfield,},
    //
    name: "ClassManagement",
    //
    data(){

        return{
            //
            tableData: [],
            //
            search: '',
            //
            dialogVisible: false,
            //
            deleteInfo:{}
        }
    },
    //
    methods:{

        //
        comeToCreateClass(){
            this.$router.push({path:"/createclass"})
        },

        //
        handleClickDelete(row){
            this.dialogVisible = true;
            this.deleteInfo = row;
        },

        //
        handleDelete(){
            this.dialogVisible = false;
            //
            let data = {
                method: "post",
                url:"/class/deleteexperimentclass",
                params: {"experimentClassNo": this.deleteInfo.experimentclassno}
            }
            //
            this.$axios.request(data)
            .then(response => {
                    //
                if(200 == response.data.code){
                    //
                    this.$message.success("");
                }
                else{
                    this.$message.error("");
                }
            })
            .catch(failResponse => {
                console.log(failResponse)
                this.$message.error("");
            })
        },

        //
        getClassList(){
            //
            let data = {
                method: "get",
                url: "/class/getexperimentclasslist",
            }
            //
            this.$axios.request(data)
            .then(response => {
                    //
                if(200 == response.data.code){
                    //
                    this.tableData = response.data.result
                }
                else{
                    this.$message.error("");
                }
            })
            .catch(failResponse => {
                console.log(failResponse)
                this.$message.error("");
            })
        },
    },
    //
    mounted(){
        //
        this.getClassList()
    }
}
</script>

<style>
.experiment-button{
    position:relative;
    left: -45.6%;
}
</style>


