<template>
    <Contentfield>
        <div>
            <el-breadcrumb separator="/">
                <el-breadcrumb-item :to="{ path: '/showstudyfile'}"></el-breadcrumb-item>
                <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
            <br/>

            <el-table
            :data="tableData.filter(data => !search || data.materialname.toLowerCase().includes(search.toLowerCase()))"
            style="width: 100%"
            height="330">

                <el-table-column
                label=""
                prop="materialname">
                </el-table-column>

                <el-table-column
                label=""
                prop="materialno">
                </el-table-column>

                <el-table-column
                label=""
                prop="materialuploadtime">
                </el-table-column>

                <el-table-column
                align="right">
                <template slot="header">
                    <el-input
                    v-model="search"
                    size="mini"
                    placeholder=""/>
                </template>
                <template slot-scope="scope">
                    <el-button
                    size="mini"
                    @click="handleEdit(scope.row)"></el-button>
                </template>
                </el-table-column>
            </el-table>

            <br/>

            <el-upload
            class="upload-button"
            ref="upload"
            action=""
            :limit="1"
            :on-exceed="handleExceed"
            :file-list="fileList"
            :http-request="onUpload"
            :auto-upload="false">
                <el-button slot="trigger" size="small" type="primary"></el-button>
                <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload"></el-button>
                <div slot="tip" class="el-upload__tip">10Mb</div>
            </el-upload>
        </div>
    </Contentfield>
</template>

<script>
import Contentfield from "@/components/AdminCom/Contentfield.vue";
export default {

    //
    components: { Contentfield,},

    //
    name: "ExperimentFile",

    //
    data(){

        return{
            //
            tableData: [],
            //
            search: '',
            //
            nowPath:"",
            //
            fileList:[],
            //
            nowTime:""
        }
    },

    //
    methods: {
        
        //
        handleEdit(row) {
            let link = document.createElement('a');
            link.style.display = 'none';
            link.href = "http://1.117.227.19:8443/api/studymaterial/getstudymaterial?materialNo=" + row.materialno;
            link.download = row.materialname;
            //document.body.appendChild(link);
            link.click();
        },

        //
        getStudyMaterialList(){
            //
            let data = {
                method: "get",
                url: "/studymaterial/getstudymateriallist",
                params: {"experimentName": this.nowPath}
            }
            //
            this.$axios.request(data)
            .then(response => {
                    //
                if(200 == response.data.code){
                    //
                    this.tableData = response.data.result
                }
                else{
                    this.$message.error("");
                }
            })
            .catch(failResponse => {
                console.log(failResponse)
                this.$message.error("");
            })
        },

        //
        handleExceed(){
            this.$message.warning("");
        },

        //
        submitUpload(){
            this.$refs.upload.submit();
        },

        //
        getNowTime(){
            //
            let nowDate = new Date()
            let year = nowDate.getFullYear()
            let month = nowDate.getMonth() + 1
            let day = nowDate.getDate()
            this.nowTime =  year + "-" + month + "-" + day
        },

        //
        onUpload(file){
            //
            this.getNowTime()
            //
            let formData = new FormData();
            formData.append("studyFile", file.file);
            //
            let data = {
                method: "post",
                url: "/studymaterial/submitstudymaterial",
                data: formData,
                params: {
                    "fileName": file.file.name,
                    "pathName": this.nowPath,
                    "uploadTime": this.nowTime
                },
                headers: {
                "Content-Type": "multipart/form-data",
                },
            }
            //
            this.$axios.request(data)
            .then(response => {
                    //
                if(200 == response.data.code){
                     this.$message.success("");
                }
                else{
                    this.$message.error("");
                }
            })
            .catch(failResponse => {
                console.log(failResponse)
                this.$message.error("");
            })
        }
    },

    //
    mounted(){
        //(//)
        this.nowPath = "/" + this.$route.query.experimentname + "/"
        //
        this.getStudyMaterialList()
    }
}
</script>

<style scoped>

.upload-button{
    position:relative;
    left: -43.5%;
}
</style>


