<template>
    <Contentfield>
        <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/managefile'}"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <br/>

        <el-table
            :data="tableData.filter(data => !search || data.materialname.toLowerCase().includes(search.toLowerCase()))"
            style="width: 100%"
            height="330">

            <el-table-column
            label=""
            prop="materialname">
            </el-table-column>

            <el-table-column
            label=""
            prop="materialno">
            </el-table-column>

            <el-table-column
            label=""
            prop="materialuploadtime">
            </el-table-column>

            <el-table-column
            align="right">
            <template slot="header">
                <el-input
                v-model="search"
                size="mini"
                placeholder=""/>
            </template>
            <template slot-scope="scope">
                <el-button
                size="mini"
                type="warning"
                @click="handleMove(scope.row)"></el-button>

                <el-button
                size="mini"
                type="danger"
                @click="handleDelete(scope.row)"></el-button>
            </template>
            </el-table-column>
        </el-table>

        <div class="move-file-select">
            <h1></h1>
            <el-select v-model="newPath" placeholder="">
                <el-option
                v-for="item in experimentList"
                :key="item.value"
                :label="item.label"
                :value="item.label">
                </el-option>
            </el-select>
        </div>

        <el-dialog
        title=""
        :visible.sync="fileDialogVisible"
        width="30%">
            <span></span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="fileDialogVisible = false"> </el-button>
                <el-button type="primary" @click="doDelete"> </el-button>
            </span>
        </el-dialog>

        <el-dialog
        title=""
        :visible.sync="moveDialogVisible"
        width="30%">
            <span>"{{this.newPath}}"</span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="moveDialogVisible = false"> </el-button>
                <el-button type="primary" @click="doMove"> </el-button>
            </span>
        </el-dialog>
    </Contentfield>
</template>

<script>
import Contentfield from "@/components/AdminCom/Contentfield.vue";
export default {

    //
    components: { Contentfield,},

    //
    name: "ManageExperimentFile",

    //
    data(){

        return{
            //
            tableData: [],
            //
            search: '',
            //
            newPath:"",
            //
            nowPath:"",
            //
            fileDialogVisible:false,
            //
            deleteInfo:{},
            //
            moveDialogVisible:false,
            //
            moveInfo:{},
            //
            experimentList:[]
        }
    },

    //
    methods: {
        
        //
        doDelete(){
            //
            this.fileDialogVisible = false
            //
            let data = {
                method: "post",
                url: "/studymaterial/deletestudymaterial",
                params: {"materialNo": this.deleteInfo.materialno}
            }
            //
            this.$axios.request(data)
            .then(response => {
                    //
                if(200 == response.data.code){

                    this.$message.success("");
                }
                else{
                    this.$message.error("");
                }
            })
            .catch(failResponse => {
                console.log(failResponse)
                this.$message.error("");
            })
        },

        //
        doMove(){
            //
            this.moveDialogVisible = false
             //
             let data = {
                method: "post",
                url: "/studymaterial/changestudymaterialpath",
                params: {
                    "materialNo": this.moveInfo.materialno,
                    "materialPath": "/" + this.newPath + "/"
                }
            }
            //
            this.$axios.request(data)
            .then(response => {
                    //
                if(200 == response.data.code){

                    this.$message.success("");
                }
                else{
                    this.$message.error("");
                }
            })
            .catch(failResponse => {
                console.log(failResponse)
                this.$message.error("");
            })
        },

        //
        handleDelete(row){
            this.deleteInfo = row
            this.fileDialogVisible = true
        },

        //
        handleMove(row){
            this.moveInfo = row
            this.moveDialogVisible = true
        },

        //
        getStudyMaterialList(){
            //
            let data = {
                method: "get",
                url: "/studymaterial/getstudymateriallist",
                params: {"experimentName": this.nowPath}
            }
            //
            this.$axios.request(data)
            .then(response => {
                    //
                if(200 == response.data.code){
                    //
                    this.tableData = response.data.result
                }
                else{
                    this.$message.error("");
                }
            })
            .catch(failResponse => {
                console.log(failResponse)
                this.$message.error("");
            })
        },
    },

    //
    mounted() {
        //(//)
        this.nowPath = "/" + this.$route.query.experimentname + "/"
        //
        this.newPath = this.$route.query.experimentname
        //
        this.experimentList = this.$route.query.list
        //
        this.getStudyMaterialList()
    },
}
</script>

<style scoped>

.move-file-select{
    position:relative;
    left: -40.5%;
}
</style>


