<template>
  <div>
    <!--  -->
    <div class="mainbox">
      <input type="checkbox" id="check" />
      <label for="check">Share</label>
      <div class="media-icons">
        <!-- li -->
        <a href="#"><i class="fab fa-qq" @click="shareToQQRom()"></i></a>
        <a href="#"><i class="fab fa-weixin" @click="shareToWeChat()"></i></a>
        <a href="#"><i class="fab fa-weibo" @click="shareToMicroblog()"></i></a>
      </div>
    </div>
    <el-dialog title="" :visible.sync="dialogFormVisible">
      <QRCode :uuurl="shareUrl"></QRCode>
      <div slot="footer" class="dialog-footer" >
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="dialogFormVisible = false"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import QRCode from "./QRcode";
export default {
  //props: ["sysInfo"], //
  components: { QRCode },
  data() {
    return {
      dialogFormVisible: false,
      shareUrl: location.href,
      sysInfo: "",
    };
  },
  methods: {
    /**
     * 
     */
    shareToMicroblog() {
      let _this = this;
      //,
      window.open(
        "https://service.weibo.com/share/share.php?url=" +
          encodeURIComponent(this.shareUrl) +
          "&title=" +
          _this.sysInfo
      );
    },
    /**
     * qq
     */
    shareToQQRom() {
      let _this = this;
      //
      window.open(
        "https://sns.qzone.qq.com/cgi-bin/qzshare/cgi_qzshare_onekey?url=" +
          encodeURIComponent(_this.shareUrl) +
          "&title=" +
          _this.sysInfo +
          "&summary=" +
          _this.sysInfo
      );
    },
    /**
     * 
     */
    shareToWeChat() {
      this.dialogFormVisible = true;
      //localStorage.setItem("shareUrl", this.shareUrl); // URL
      //let url = location.href.split("#")[0] + "#/Share/toWechat"; // #/Share/toWechat: 
      //window.open(url);
    },
  },
};
</script>

<style scoped>
@import "https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.3/css/all.min.css";
/*  */
body {
  background: radial-gradient(#333, #000);
  height: 100vh;
}

/* mainbox */
.mainbox {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

/* label */
label {
  position: relative;
  background: #fff;
  width: 150px;
  height: 50px;
  border-radius: 35px;
  line-height: 50px;
  text-align: center;
  font-size: 22px;
  font-weight: 500;
  display: block;
  color: #333;
  cursor: pointer;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.5);
  transition: all 0.3s ease;
}

/* hover */
label:hover {
  letter-spacing: 2px;
}

/* before */
label::before {
  content: "Cancel";
  position: absolute;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
  background: #fff;
  border-radius: 35px;
  /* 0 */
  opacity: 0;
}

/* input */
#check {
  display: none;
}

/* input1 */
#check:checked ~ label::before {
  opacity: 1;
}

/*  */

.media-icons {
  position: absolute;
  left: 50%;
  top: -120px;
  transform: translateX(-50%);
  background: #fff;
  width: 140%;
  height: 110%;
  /* text-align */
  text-align: center;
  /*  */
  display: flex;
  /* align-items  ok */
  align-items: center;
  justify-content: space-evenly;
  border-radius: 35px;
  padding: 4px;
  z-index: 1;
  opacity: 0;
  pointer-events: none;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.5);
  transition: all 0.3s ease;
}

/* input */
#check:checked ~ .media-icons {
  opacity: 1;
  pointer-events: auto;
  top: -84px;
}

/* 45 */
.media-icons::before {
  content: "";
  width: 18px;
  height: 18px;
  position: absolute;
  left: 50%;
  background-color: #fff;
  bottom: -9px;
  transform: translateX(-50%) rotate(45deg);
  z-index: -1;
}

/* a */
.media-icons a {
  font-size: 22px;
  color: #333;
  transition: all 0.3s ease;
}

.media-icons a:hover {
  transform: translateY(-2px);
}

/* icon */
.media-icons a:nth-child(1):hover {
  color: #000000;
}
.media-icons a:nth-child(2):hover {
  color: #7bb32e;
}
.media-icons a:nth-child(3):hover {
  color: #df2029;
}
.media-icons a:nth-child(4):hover {
  color: #000000;
}
</style>



