<template>
    <div>
        <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/problemmanagement'}"></el-breadcrumb-item>
            <!-- <el-breadcrumb-item :to="{ path: '/experimentproblem'}"></el-breadcrumb-item> -->
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>

        <br/>
        <div class="experiment-div">      
            <el-row :gutter="12">  
                <el-col :span="22">
                    <h1 class="experiment-title"></h1>
                    <el-input
                    type="textarea"
                    :rows="3"
                    placeholder="(100)"
                    v-model="problemInfo.problemdescription">
                    </el-input>
                </el-col>             
                <el-col :span="22">
                    <h1 class="experiment-title">A</h1>
                    <el-input
                    type="textarea"
                    :rows="2"
                    placeholder="A(50)"
                    v-model="problemInfo.problemoptiona">
                    </el-input>
                </el-col>   
                <el-col :span="22">
                    <h1 class="experiment-title">B</h1>
                    <el-input
                    type="textarea"
                    :rows="2"
                    placeholder="B(50)"
                    v-model="problemInfo.problemoptionb">
                    </el-input>
                </el-col>   
                <el-col :span="22">
                    <h1 class="experiment-title">C</h1>
                    <el-input
                    type="textarea"
                    :rows="2"
                    placeholder="C(50)"
                    v-model="problemInfo.problemoptionc">
                    </el-input>
                </el-col>   
                <el-col :span="22">
                    <h1 class="experiment-title">D</h1>
                    <el-input
                    type="textarea"
                    :rows="2"
                    placeholder="D(50)"
                    v-model="problemInfo.problemoptiond">
                    </el-input>
                </el-col>   
                <el-col :span="22">
                    <h1 class="experiment-title"></h1>
                    <el-select 
                    v-model="problemInfo.problemanswer" 
                    placeholder=""
                    class="problem-select">
                        <el-option
                          v-for="item in options"
                          :key="item.value"
                          :label="item.label"
                          :value="item.value">
                        </el-option>
                      </el-select>
                </el-col>   
                <el-col :span="22">
                    <br/>
                    <el-button 
                    type="primary" 
                    plain 
                    @click="CreateProblem"
                    ></el-button>    
                </el-col> 
            </el-row>                
        </div>
    </div>
</template>

<script>
export default {
    //
    name: "CreateProblem",
    //
    data(){

        return{
            //
            problemInfo:{
                problemdescription:"",
                problemno:0,
                experimentno:0,
                experimentname:"",
                problemoptiona:"",
                problemoptionb:"",
                problemoptionc:"",
                problemoptiond:"",
                problemanswer:"",
            },
            //
            experimentNo:0,
            //
            experimentName:"",
            //
            options: [{
            value: 'A',
            label: 'A'
            }, 
            {
            value: 'B',
            label: 'B'
            }, 
            {
            value: 'C',
            label: 'C'
            }, 
            {
            value: 'D',
            label: 'D'
            }],
        }
    },
    //
    methods:{
        //
        CreateProblem(){
            //
            if("" == this.problemInfo.problemdescription){
                this.$message.success("")
            }
            //
            this.problemInfo.experimentname = this.experimentName
            this.problemInfo.experimentno = this.experimentNo
            //
            let data = {
                    method: "post",
                    url:"/exercise/createexerciseproblem",
                    data: this.problemInfo
            }
            //
            this.$axios.request(data)
            .then(response => {
                    //
                if(200 == response.data.code){
                    //
                    this.$message.success("");
                }
                else{
                    this.$message.error("");
                }
            })
            .catch(failResponse => {
                console.log(failResponse)
                this.$message.error("");
            })
        }
    },
    //
    mounted(){
        //
        this.experimentNo = this.$route.query.experimentNo
        this.experimentName = this.$route.query.experimentName
        console.log(this.experimentNo)
        console.log(this.experimentName)
    }
}
</script>

<style scoped>
.experiment-title{
    font-family:"PingFang SC";
    font-size:25px;
    color:black;
    text-align:left
}

.experiment-div{
    width: 100%;
    height: 420px;
}
.problem-select{
    position:relative;
    left: -40%;
}
</style>


