<template>
    <Contentfield>
        <div>
            <el-breadcrumb separator="/">
              <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <br/>

        <h1 style="text-align:left"></h1>
        <el-table
            :data="classExperimentTable.filter(data => !classSearch || data.experimentname.toLowerCase().includes(classSearch.toLowerCase()))"
            style="width: 100%"
            height="380">
            <el-table-column
            label=""
            prop="experimentno">
            </el-table-column>
            <el-table-column
            label=""
            prop="experimentname">
            </el-table-column>
            <el-table-column
            align="right">
            <template slot="header">
                <el-input
                v-model="classSearch"
                size="mini"
                placeholder=""/>
            </template>
            <template slot-scope="scope">

                <el-button
                size="mini"
                type="danger"
                @click="handleClickDelete(scope.row)"></el-button>

                <el-button
                size="mini"
                @click="handleCheck(scope.row)"></el-button>

            </template>
            </el-table-column>
        </el-table>

        <h1 style="text-align:left"></h1>
        <el-table
            :data="allExperimentTable.filter(data => !allSearch || data.experimentname.toLowerCase().includes(allSearch.toLowerCase()))"
            style="width: 100%"
            height="400"
            highlight-current-row
            @current-change="handleSelectExperimentChange">

            <el-table-column
            label=""
            prop="experimentno">
            </el-table-column>

            <el-table-column
            label=""
            prop="experimentname">
            </el-table-column>


            <el-table-column
            align="right">
            <template slot="header">
                <el-input
                v-model="allSearch"
                size="mini"
                placeholder=""/>
            </template>
            <template slot-scope="scope">
                <el-button
                size="mini"
                @click="handleCheck(scope.row)"></el-button>
            </template>
            </el-table-column>
        </el-table>

        <br/>
        <el-button 
        type="primary" 
        plain 
        class="experiment-button"
        @click="addExperiment"
        ></el-button> 

        <el-dialog
        title=""
        :visible.sync="dialogVisible"
        width="30%">
            <span></span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false"> </el-button>
                <el-button type="primary" @click="handleDelete"> </el-button>
            </span>
        </el-dialog>
    </Contentfield>
</template>

<script>
import Contentfield from "@/components/AdminCom/Contentfield.vue";
export default {
    //
    components: { Contentfield,},
    //
    name: "ClassExperiment",
    //
    data(){

        return{
            //Id
            userId:this.$store.getters.userId,
            //
            experimentClassNo:0,
            //
            classExperimentTable:[],
            //
            classSearch:"",
            //
            allExperimentTable:[],
            //
            allSearch:"",
            //
            addExperimentList:{},
            //
            dialogVisible:false,
            //
            deletInfo:{}
        }
    },
    //
    methods:{

        //
        handleCheck(row){
            row["path"] = "/classexperiment"
            this.$router.push({path:'/experimentdetail',query:row})
        },

        //
        handleDelete(){
            this.dialogVisible = false
             //
             let data = {
                method: "post",
                url:"/class/deleteclassexperiment",
                params: {"experimentNo": this.deletInfo.experimentno, "experimentClassNo": this.experimentClassNo}
            }
            //
            this.$axios.request(data)
            .then(response => {
                    //
                if(200 == response.data.code){
                    //
                    this.$message.success("");
                }
                else{
                    this.$message.error("");
                }
            })
            .catch(failResponse => {
                console.log(failResponse)
                this.$message.error("");
            })
        },

        //
        addExperiment(){
            //
            if({} == this.addExperimentList){
                this.$message.error("")
                return
            }
            //
            let data = {
                method: "post",
                url:"/experiment/publishexperiment",
                params: {"experimentNo": this.addExperimentList.experimentno, "experimentClassNo": this.experimentClassNo}
            }
            //
            this.$axios.request(data)
            .then(response => {
                    //
                if(200 == response.data.code){
                    //
                    this.$message.success("");
                }
                else{
                    this.$message.error("");
                }
            })
            .catch(failResponse => {
                console.log(failResponse)
                this.$message.error("");
            })
        },

        //
        handleSelectExperimentChange(val){
            this.addExperimentList = val
        },

        //
        handleClickDelete(row){
            this.dialogVisible = true
            this.deletInfo = row
        },

        //
        getClassExperimentList(){
            //
            let data = {
                method: "get",
                url: "/experiment/getallexperimentinclass",
                params: {"userId": this.userId}
            }
            //
            this.$axios.request(data)
            .then(response => {
                //
               if(200 == response.data.code){
                //
                   this.classExperimentTable = response.data.result;
               }
               else{
                    this.$message.error("");
               }
            })
            .catch(failResponse => {
                console.log(failResponse)
                this.$message.error("");
            })
        },

        //
        getexperimentList(){
            //
            let data = {
                method: "get",
                url: "/experiment/getexperimentlist",
            }
            //
            this.$axios.request(data)
            .then(response => {
                    //
                if(200 == response.data.code){
                    //
                    this.allExperimentTable = response.data.result
                }
                else{
                    this.$message.error("");
                }
            })
            .catch(failResponse => {
                console.log(failResponse)
                this.$message.error("");
            })
        },

        //
        getExperimentClass(){
            //
            let data = {
                method: "get",
                url: "/class/findexperimentclass",
                params: {"userId": this.userId}
            }
            //
            this.$axios.request(data)
            .then(response => {
                //
               if(200 == response.data.code){
                //
                   this.experimentClassNo = response.data.result;
               }
               else{
                    this.$message.error("");
               }
            })
            .catch(failResponse => {
                console.log(failResponse)
                this.$message.error("");
            })
        }
    },
    //
    mounted(){
        //
        this.getClassExperimentList()
        //
        this.getexperimentList()
        //
        this.getExperimentClass()
    }
}
</script>

<style scoped>
.experiment-button{
    position:relative;
    left: -44.4%;
}
</style>


