<template>
    <div>
        <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/myreport'}"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <br/>
        <div class="experiment-div">
            
            <h1 class="experiment-title">
                {{this.experimentDetail.experimentname}}
            </h1>
            <h1 class="experiment-title">
                {{this.experimentDetail.experimentno}}
            </h1>
            
            <el-row :gutter="12">  
                <el-col :span="22">
                    <h1 class="experiment-title"></h1>
                    <el-card shadow="hover">
                        <h3 class="experiment-text">{{this.experimentDetail.experimentpurpose}}</h3>
                    </el-card>
                </el-col>             
                <el-col :span="22">
                    <h1 class="experiment-title"></h1>
                    <el-card shadow="hover">
                        <h3 class="experiment-text">{{this.experimentDetail.experimentbasicknowledge}}</h3>
                    </el-card>
                </el-col>   
                <el-col :span="22">
                    <h1 class="experiment-title"></h1>
                    <el-card shadow="hover">
                        <h3 class="experiment-text">{{this.experimentDetail.experimentsteps}}</h3>
                    </el-card>
                </el-col>
                <el-col :span="22">
                    <h1 class="experiment-title"></h1>
                    <el-card shadow="hover">
                        <h3 class="experiment-text">{{this.reportInfo.experimentphenomenon}}</h3>
                    </el-card>
                </el-col>    
                <el-col :span="22">
                    <h1 class="experiment-title"></h1>
                    <el-card shadow="hover">
                        <h3 class="experiment-text">{{this.experimentDetail.experimentquestion}}</h3>
                    </el-card>
                </el-col>   
                <el-col :span="22">
                    <h1 class="experiment-title"></h1>
                    <el-card shadow="hover">
                        <h3 class="experiment-text">{{this.reportInfo.experimentperception}}</h3>
                    </el-card>
                </el-col> 
                <el-col :span="22">
                    <h1 class="experiment-title"></h1>
                    <el-card shadow="hover">
                        <h3 class="experiment-text">{{this.reportInfo.experimentcomments}}</h3>
                    </el-card>
                </el-col> 
                <el-col :span="22">
                    <h1 class="experiment-title">{{this.reportInfo.experimentgrade}}</h1>
                </el-col> 
            </el-row>       
        </div>
    </div>
</template>

<script>
export default {
    //
    name: "ReportDetail",
    //
    data(){

        return{
            //
            experimentNo:0,
            //
            experimentReportNo:0,
            //
            experimentDetail:{},
            //
            reportInfo:{},
        }
    },
    //
    methods:{
        //
        getExperimentDetail(){
            //
            let data = {
                method: "get",
                url: "/experiment/getexperiment",
                params: {"experimentNo": this.experimentNo}
            }
            //
            this.$axios.request(data)
            .then(response => {
                //
               if(200 == response.data.code){
                //
                   this.experimentDetail = response.data.result;
               }
               else{
                    this.$message.error("");
               }
            })
            .catch(failResponse => {
                console.log(failResponse)
                this.$message.error("");
            })
        },

        //
        getExperimentReportDetail(){
            //
            let data = {
                method: "get",
                url: "/experiment/getexperimentreport",
                params: {"experimentReportNo": this.experimentReportNo}
            }
             //
             this.$axios.request(data)
            .then(response => {
                //
               if(200 == response.data.code){
                //
                   this.reportInfo = response.data.result;
               }
               else{
                    this.$message.error("");
               }
            })
            .catch(failResponse => {
                console.log(failResponse)
                this.$message.error("");
            })
        }
    },
    //
    mounted(){
         //
         this.experimentNo = this.$route.query.experimentno
        //
        this.experimentReportNo = this.$route.query.experimentreportno
        //
        this.getExperimentDetail()
        //
        this.getExperimentReportDetail()
    }
}
</script>

<style scoped>
.experiment-title{
    font-family:"PingFang SC";
    font-size:25px;
    color:black;
    text-align:left
}

.experiment-div{
    width: 100%;
    height: 420px;
}

.experiment-text{
    font-family:"PingFang SC";
    font-size:15px;
    color:black;
    text-align:left;
    width: 95%;
}
</style>


