<template>
    <Contentfield>
        <div>
            <el-breadcrumb separator="/">
              <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <br/>

        <h1 style="text-align:left"></h1>
        <el-table
        :data="classUserTable.filter(data => !classSearch || data.username.toLowerCase().includes(classSearch.toLowerCase()))"
        style="width: 100%"
        height="400">

            <el-table-column
            label="ID"
            prop="userid">
            </el-table-column>

            <el-table-column
            label=""
            prop="username">
            </el-table-column>

            <el-table-column
            prop="level"
            label=""
            width="150"
            :filters="[
            { text: '', value: '' },
            { text: '', value: '' },]"
            filter-placement="bottom-end">
                <template slot-scope="scope">
                    <el-tag
                        :type="
                        scope.row.level == ''
                            ? 'warning'
                            : scope.row.level == ''
                            ? 'success'
                            : 'primary'"
                        disable-transitions
                    >{{ scope.row.level }}</el-tag>
                </template>
            </el-table-column>

            <el-table-column
            label=""
            prop="status">
            </el-table-column>

            <el-table-column
            align="right">

                <template slot="header">
                    <el-input
                    v-model="classSearch"
                    size="mini"
                    placeholder=""/>
                </template>

                <template slot-scope="scope">
                    <el-button
                    size="mini"
                    type="danger"
                    @click="handleClickDelete(scope.row)"></el-button>
                </template>

            </el-table-column>
        </el-table>

        <h1 style="text-align:left"></h1>
        <el-table
        :data="allUserTable.filter(data => !allSearch || data.username.toLowerCase().includes(allSearch.toLowerCase()))"
        style="width: 100%"
        height="400"
        highlight-current-row
        @current-change="handleSelectUserChange">

            <el-table-column
            label="ID"
            prop="userid">
            </el-table-column>

            <el-table-column
            label=""
            prop="username">
            </el-table-column>

            <el-table-column
            prop="level"
            label=""
            width="150"
            :filters="[
            { text: '', value: '' },
            { text: '', value: '' },]"
            filter-placement="bottom-end">
                <template slot-scope="scope">
                    <el-tag
                        :type="
                        scope.row.level == ''
                            ? 'warning'
                            : scope.row.level == ''
                            ? 'success'
                            : 'primary'"
                        disable-transitions
                    >{{ scope.row.level }}</el-tag>
                </template>
            </el-table-column>

            <el-table-column
            label=""
            prop="status">
            </el-table-column>

            <el-table-column
            align="right">

                <template slot="header">
                    <el-input
                    v-model="allSearch"
                    size="mini"
                    placeholder=""/>
                </template>
            </el-table-column>
        </el-table>

        <br/>
        <el-button 
        type="primary" 
        plain 
        class="experiment-button"
        @click="addClassUser"
        ></el-button>

        <el-dialog
        title=""
        :visible.sync="dialogVisible"
        width="30%">
            <span></span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false"> </el-button>
                <el-button type="primary" @click="handleDelete"> </el-button>
            </span>
        </el-dialog>
    </Contentfield>
</template>

<script>
import Contentfield from "@/components/AdminCom/Contentfield.vue";
export default {
    //
    components: { Contentfield,},
    //
    name: "ClassStudent",
    //
    data(){

        return{
            //Id
            userId:this.$store.getters.userId,
            //
            experimentClassNo:0,
            //
            classUserTable:[],
            //
            classSearch:"",
            //
            allUserTable:[],
            //
            allSearch:"",
            //
            addUser:{},
            //
            dialogVisible:false,
            //
            deletInfo:{}
        }
    },
    //
    methods:{

        //
        addClassUser(){
            //
            if({} == this.addUser){
                this.$message.error("")
                return
            }
            //
            let data = {
                method: "post",
                url:"/class/addclassmember",
                params: {"userId": this.addUser.userid, "experimentClassNo": this.experimentClassNo}
            }
            //
            this.$axios.request(data)
            .then(response => {
                    //
                if(200 == response.data.code){
                    //
                    this.$message.success("");
                }
                else{
                    this.$message.error("");
                }
            })
            .catch(failResponse => {
                console.log(failResponse)
                this.$message.error("");
            })
        },

        //
        handleDelete(){
            this.dialogVisible = false
             //
             let data = {
                method: "post",
                url:"/class/deleteclassmember",
                params: {"userId": this.deletInfo.userid, "experimentClassNo": this.experimentClassNo}
            }
            //
            this.$axios.request(data)
            .then(response => {
                    //
                if(200 == response.data.code){
                    //
                    this.$message.success("");
                }
                else{
                    this.$message.error("");
                }
            })
            .catch(failResponse => {
                console.log(failResponse)
                this.$message.error("");
            })
        },

        //
        handleClickDelete(row){
            if((row.level == "") || (row.level == "")){
                this.$message.error("")
                return
            }
            this.dialogVisible = true
            this.deletInfo = row
        },

        //
        handleSelectUserChange(val){
            this.addUser = val
        },

        //
        getExperimentClass(){
            //
            let data = {
                method: "get",
                url: "/class/findexperimentclass",
                params: {"userId": this.userId}
            }
            //
            this.$axios.request(data)
            .then(response => {
                //
               if(200 == response.data.code){
                //
                   this.experimentClassNo = response.data.result;
               }
               else{
                    this.$message.error("");
               }
            })
            .catch(failResponse => {
                console.log(failResponse)
                this.$message.error("");
            })
        },

        //
        getClassUserList(){
            //
            let data = {
                method: "get",
                url: "/experiment/getallstudentinclass",
                params: {"userId": this.userId}
            }
            //
            this.$axios.request(data)
            .then(response => {
                //
               if(200 == response.data.code){
                //
                   this.classUserTable = response.data.result;
               }
               else{
                    this.$message.error("");
               }
            })
            .catch(failResponse => {
                console.log(failResponse)
                this.$message.error("");
            })
        },

        //
        getAllUserList(){
            //
            let data = {
                method: "get",
                url: "/experiment/studentnoinclass",
            }
            //
            this.$axios.request(data)
            .then(response => {
                    //
                if(200 == response.data.code){
                    //
                    this.allUserTable = response.data.result
                }
                else{
                    this.$message.error("");
                }
            })
            .catch(failResponse => {
                console.log(failResponse)
                this.$message.error("");
            })
        },
    },
    //
    mounted(){
        //
        this.getExperimentClass()
        //
        this.getClassUserList()
        //
        this.getAllUserList()
    }
}
</script>

<style scoped>
.experiment-button{
    position:relative;
    left: -45.6%;
}
</style>


